$(document).ready(function(){

if(document.querySelector('.top-slider .owl-carousel')){
	$('.top-slider .owl-carousel').owlCarousel({
 		items : 1,
 		loop:true,
 		nav:true,
 		pagination: false,
 		navText: [
 		"<i class='my-arrow-left'></i>",
 		"<i class='my-arrow-right'></i>"
 		],
 		// autoplay: true,
 		// autoplayTimeout: 3000,
 	});
 }
	if(document.querySelector('.news-slider .owl-carousel')){
	 	$('.news-slider .owl-carousel').owlCarousel({
	 		items : 3,
	 		loop:($('.news-slider .owl-carousel').children().length)==1 ? false:true,
	 		nav:true,
	 		pagination: false,
	 		navText: [
	 		"<i class='my-arrow-left glyphicon glyphicon-menu-left'></i>",
	 		"<i class='my-arrow-right glyphicon glyphicon-menu-right'></i>"
	 		],
	 		// autoplay: true,
	 		// autoplayTimeout: 3000,
	 		responsive : {
		    // breakpoint from 0 up
			    0 : {
			        items : 1,
			    },
			    // breakpoint from 480 up
			    480 : {
			        items : 2,
			    },
			    // breakpoint from 768 up
			    768 : {
			        items : 3,
			    },
			    // breakpoint from 992 up
			    // 992 : {
			    //     items : 3,
			    // }
			}
	 	});

	}
});
